export const init = () => {
    const modals = document.querySelectorAll('#page-mod-booking-optionview [id^="sbPrePageModal"]');

    if (!modals) {
        return;
    }
    modals.forEach(modal => {
        const parent = modal.closest('#region-main');
        if (parent) {
            parent.appendChild(modal);
        }
    });
};
